.Header {
    background-color: #ccc;
    padding: 20px 30px;
    
    a {
      text-decoration: none;
      color: black;
      &.active {
        text-decoration: underline;
      }
      &.highlight {
        background-color: yellow;
        color: red;
      }
      &:hover {
        color: rgb(61, 61, 61);
      }
      &:active {
        color: black;
      }
    }
nav {
  display: flex;
  justify-content: space-between;
  ul {
    display: flex;
    li {
      margin-left: 20px;
      &:last-child {
        border-left: 1px solid #a5a5a5;
        padding-left: 20px;
      }
    }
  }
}
}
